.down-app-area{
    .mod-1{
        background: #f8f8f8;
        padding: 80px 0;
        width: 100%;
        margin-top: 80px;
        .wbox{
            padding: 20px 0 0 335px
        }
        .lbox{
            margin-right: 15px;
            img{
                padding: 6px;
                border: 1px solid #cccccc;
                border-radius: 5px;
            }
        }
        .rbox{
            a:hover{
                text-decoration: none;
            }
            .list{
                li{
                    line-height: 10px;
                    width: 190px;
                    height: 60px;
                    background: #333333;
                    margin-bottom: 10px;
                    border-radius: 5px;
                    color: #fff;
                    &:hover{
                        background: #666666;
                    }
                    .icon-mod{
                        width: 28px;
                        height: 32px;
                        float: left;
                        margin: 15px 0 0 22px;

                    }
                    .icon-iphone{
                        background: url(../../images/icon-iphone-28x32.png);
                    }
                    .icon-Android{
                        background: url(../../images/icon-ad-28x32.png);
                    }
                    .txt{
                        margin: 7px 0 0 18px;
                        float:left;display:inline;  
                    }
                }
            }


        }
    }


    .mod-2{
        margin-top: 80px;
        .list{
            padding: 30px 0 0 215px;
            li{
                float: left;
                width: 160px;
                text-align: center;
                margin-left: 85px;
                .txt{
                    padding-top: 10px;
                    font-size: 14px;
                }
                img{
                    padding: 6px;
                    border: 1px solid #cccccc;
                    border-radius: 5px;
                }

            }
        }
        .tel{
            text-align: center;
            font-size: 24px;
            margin: 70px 0 70px 0;
        }
    }
}